
* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: sans-serif !important;
    font-size: 14px;
    line-height: 17px;
}

.App {
    display: flex;
    width: 100vw;
    height: 100vh;
    overflow: hidden;
}

.Sidebar {
    background-color: #ffff;
    width: 300px;
    min-width: 210px;
    height: 100%;
}
.Main {
    height: 100%;
    width: 100%;
    background-color: #f2f2f2;
    position: relative;
}

.User {
    display: flex;
    align-items: center;
    justify-content: space-around;
    padding: 1rem;
    border: 1px solid #ebebeb;
}

.User .logo img {
    width: 50px;
}

.User .info p {
    font-size: 1.1rem;
    font-weight: bold;
}

.User .info a {
    text-decoration: none;
    display: flex;
    justify-content: flex-end;
}

.Modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background: rgba(0, 0, 0, 0.5);
    z-index: 99;
}

.Modal .container {
    position: absolute;
    width: 30%;
    top: 25%;
    left: 50%;
    transform: translate(-50%, -25%);
}
.AddNewTodo {
    border-bottom: 1px solid #ebebeb;
}

.AddNewTodo .btn {
    padding: 1rem;
    width: 100%;
    height: 70px;
}
.AddNewTodo .btn button {
    width: 100%;
    height: 100%;
    border: none;
    border-radius: 4px;
    background-color: #0080ff;
    font-size: 1rem;
    color: white;
    font-weight: bold;
    cursor: pointer;
}

.AddNewTodo .btn button:hover {
    opacity: 0.7;
    transition: all 300ms ease-in-out;
}

.TodoForm{
    padding: 1rem 1rem calc(1rem + 40px) 1rem;
    width: 100%;
    background: white;
    border-radius: 4px;
}

.TodoForm .text input {
    padding: 1rem;
    width: 100%;
    margin-bottom: 0, 5rem;
    border: none;
    font-size: 1.2rem;
}
.TodoForm .text input:focus {
    outline: none;
    border-bottom: 1px solid #0080ff;
}

.TodoForm .remind {
    display: flex;
    align-items: center;
    padding: 0, 5rem;
}
.TodoForm p {
    margin-left: 10px;
}
.TodoForm .title {
    display: flex;
    align-items: center;
    margin-bottom: 5px;
}
.TodoForm .pick-day {
    padding: 1.5rem 1.5rem 0 1.5rem;
}
.TodoForm .pick-time {
    padding: 1.5rem;
}
.TodoForm .pick-project {
    padding: 0.5rem;
}
.TodoForm .pick-project .projects {
    display: flex;
    flex-wrap: wrap;
    padding: 1rem 1rem 0 1rem;
    height: 55px;
    overflow: auto;
}
.TodoForm .pick-project .projects .project {
    border: 1px solid #0080ff;
    border-radius: 5px;
    padding: 5px;
    margin: 5px;
    cursor: pointer;
}
.TodoForm .pick-project .projects .active {
    background: #0080ff;
    color: white;
}



.TodoForm .pick-priority {
    padding: 0.5rem;
}
.TodoForm .pick-priority .prioritys {
    display: flex;
    flex-wrap: wrap;
    justify-content:right;
    padding: 1rem 1rem 0 1rem;
}
.TodoForm .pick-priority .prioritys .priority {
    border: 1px solid #0080ff;
    border-radius: 5px;
    padding: 5px;
    margin: 5px;
    cursor: pointer;
 
}
.TodoForm .pick-priority .prioritys .active {
    background: #0080ff;
    color: white;
}



.TodoForm .cancel {
    position: absolute;
    top: 0;
    right: 5px;
    cursor: pointer;
}
.TodoForm .confirm {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
}
.TodoForm .confirm button {
    width: 100%;
    height: 40px;
    background: #0080ff;
    color: white;
    font-size: 1.1rem;
    border: none;
    border-radius: 0 0 4px 4px;
    cursor: pointer;
}

.Calendar {
    padding: 1rem;
    border-bottom: 1px solid #ebebeb;
}

.Calendar .header {
    display: flex;
    align-items: center;
}
.Calendar .header .title {
    display: flex;
    align-items: center;
    flex: 1;
}
.Calendar .header .title p {
    font-size: 1.1rem;
    margin-left: 5px;
    font-weight: bold;
}

.Calendar .header .btns span {
    border-radius: 50%;
    cursor: pointer;
    width: 25px;
    height: 25px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.Calendar .header .btns span:hover {
    background-color: #ebebeb;
    transition: all 300ms ease-in-out;
}
.Calendar .items {
    padding: 0.5rem;
}
.Calendar .items .item {
    padding: 0.25rem;
    margin: 5px;
    font-size: 1rem;
    cursor: pointer;
}

.Calendar .items .item.active {
    color: #0080ff;
}

.Projects {
    padding: 1rem;
    border-bottom: 1px solid #ebebeb;
}
.Projects .header {
    display: flex;
    align-items: center;
}
.Projects .header .title {
    display: flex;
    align-items: center;
    flex: 1;
}
.Projects .header .title p {
    margin-left: 5px;
    font-size: 1.1rem;
    font-weight: bold;
}
.Projects .header .btns {
    display: flex;
    align-items: center;
}
.Projects .header .btns span.edit {
    margin-right: 5px;
}
.Projects .header .btns span {
    border-radius: 50%;
    cursor: pointer;
    width: 25px;
    height: 25px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.Projects .header .btns span:hover {
    background-color: #ebebeb;
    transition: all 300ms ease-in-out;
}
.Projects .items {
    padding: 0.5rem;
}
/* Project */
.Project {
    position: relative;
    display: flex;
    align-items: center;
    padding: 0.25rem;
    margin: 5px;
    font-size: 1rem;
}
.Project .name {
    cursor: pointer;
    word-break: break-all;
}
.Project .name.active {
    color:#0080ff;
}
.Project .btns {
    display: flex;
    align-items: center;
}
.Project .btns .total-todos {
    position: absolute;
    right: 0;
    background: #ebebeb;
    width: 25px;
    height: 25px;
    font-size: 0.9rem;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
}
.Project .btns .edit-delete {
    position: absolute;
    right: 0;
    display: flex;
}
.Project .btns .edit-delete span {
    border-radius: 50%;
    cursor: pointer;
    width: 25px;
    height: 25px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: background 300ms ease-in-out;
}
.Project .btns .edit-delete span.edit:hover {
    background: #acffac;
}
.Project .btns .edit-delete span.delete:hover {
    background: #ffacac;
}

/* AddNewProject */
.AddNewProject .add-button span {
    margin-right: 5px;
}
/* ProjectForm */
.ProjectForm {
    background-color: white;
    padding: 1rem;
    border-radius: 4px;
    position: relative;
    padding-bottom: 40px;
}
.ProjectForm input {
    width: 400px;
    height: 60px;
    margin: 15px 0;
    border: none;
    font-size: 1.2rem;
}
.ProjectForm input:focus {
    outline: none;
    border-bottom: 1px solid #0080ff;
}
.ProjectForm button {
    position: absolute;
    bottom: 0;
    height: 40px;
    width: 50%;
    border: none;
    color: white;
    font-size: 1rem;
    font-weight: bold;
    cursor: pointer;
    transition: filter 300ms ease-in-out;
}
.ProjectForm button.cancel {
    left: 0;
    background-color: #bebebe;
}
.ProjectForm button.confirm {
    right: 0;
    background-color: #0080ff;
}
.ProjectForm button:hover {
    filter: brightness(1.1);
}

/* Todos */
.Todos {
    width: 300px;
    height: 87%;
    background-color: white;

    position: absolute;
    bottom: 0px;
    left: 40px;

    border-radius: 4px;
    opacity: 0.9;
    padding: 0 0.5rem 0.5rem 0.5rem;
    overflow-y:auto;
}


.InfoLesson{
    width: 300px;
    height: 87%;
    background-color: white;

    position: absolute;
    bottom: 0px;
    left: calc(40px + 300px + 40px);

    border-radius: 4px;
    opacity: 0.9;
    padding: 0 0.5rem 0.5rem 0.5rem;
    overflow-y:auto;
}


.Todos .selected-project {
    display: flex;
    margin: 1rem 0;
    justify-content: space-between;
    text-transform: capitalize;
    font-size: 1.2rem;
    font-weight: bold;
    padding-bottom: 2px;

    border-bottom: 1px solid #eeeeee;
}
.Todos .todos {
    overflow-y: auto;
}
.Todo {
    padding: 0.5rem 1rem;
}
.Todo .todo-container {
    display: flex;
    align-items: center;
}
.Todo .todo-container span {
    cursor: pointer;
}
.Todo .todo-container .text {
    position: relative;
    word-break: break-word;
    flex: 1;
    margin: 0 10px;
    cursor: pointer;
}

.Todo .todo-container .text span {
    font-size: 0.8rem;
    color: #707070;
    /* #bebebe */
}
.Todo .todo-container .text  .timeTitle {
    font-size: 0.8rem;
    color:  #707070;
    /* #bebebe */
}


.Todo .todo-container .text span .itemTitle[data-priority='0'] {
    color: #7963f3;
}
.Todo .todo-container .text span .itemTitle[data-priority='1'] {
    color: #563edd;
}
.Todo .todo-container .text span .itemTitle[data-priority='2'] {
    color: #3825a8;
}
.Todo .todo-container .text .line {
    height: 1px;
    background: #bebebe;
    position: absolute;
    top: 25%;
    width: 0;
    transition: width 250ms ease-in-out;
}
.Todo .todo-container .text .line-through {
    width: 100%;
}
.Todo .todo-container .add-to-next-day {
    margin-right: 10px;
}
.EditTodo {
  position: absolute;
  bottom: 40px;
  /* left: calc(40px + 300px + 40px); */
  background-color: white;

  height: 80%;
  width: 330px;

  border-radius: 4px;
  opacity: 0.9;
}

.TodoForm .remind .check {
    margin-left: 10px;
 }

.EditTodo .header {
    display: flex;
    justify-content: space-between;
  padding: 1rem 0.5rem;

  font-size: 1.2rem;
  font-weight: bold;

  border-bottom: 1px solid #eeeeee;
  background: white;
}

.Next7Days .day {
margin: 5px;
display: flex;
}
.Next7Days .day .name{
    font-weight: bold;
    font-size: 1rem;
}

.Next7Days .day .total-todos {
    margin-left: 10px;
}

.mainEditTodo{
    position: absolute;
    top: 137px;
}

.left {
    
    left: calc(40px + 300px + 40px);
}
.right{
    left: calc(40px + 580px + 40px);
}


.dopInfoLesson{
 
    width: 300px;
    height: 87%;
    background-color: white;

    position: absolute;
    bottom: 0px;
    left: 350px;

    border-radius: 4px;
